<style scoped>
    .income-widget {

    }

    .debug-table {
        margin-top: 20px;
        position: absolute;
    }

    a {
        color: #FFF;
    }

    .addone {
        background: #FFF;
        border: 0;
        height: 32px;
        width: 100%;
        color: #00BCD4;
        font-size: 32px;
        outline: none;
        border-top: 1px solid #00bcd4;
        cursor: pointer;
    }
</style>

<template>
    <div class="widget income-widget">
        <h1>Income</h1>
        <div class="widget-body">
            <table class="income-table table table-lined">
                <app-income-item
                        v-for="(item, index) in incomeArray"
                        :key="index"
                        :item="item"
                ></app-income-item>
            </table>

            <button @click="newItem" class="addone">+</button>
        </div>
    </div>

</template>

<script>
	import authService from '@/services/auth-service';
	import dateService from '@/services/date-service';
	import incomeService from '@/services/income-service';


	export default {
		name: 'app-income-widget',

		created: function () {
			incomeService.events.$on('updated', (data) => {
				this.$data.income = data.income;
				this.$data.categories = data.categories;
			});
		},
		computed: {
			incomeArray: function () {
				const arr = [];
				for (let item in this.$data.income) {
					arr.push(this.$data.income[item]);
				}

				arr.sort((a, b)=>a.date-b.date);
				return arr;
			}
		},
		methods: {
			newItem: function () {
				incomeService.addNewItem();
				setTimeout(()=>{
					const inputs = this.$el.querySelectorAll('input.amount');
					inputs[inputs.length-1].focus();
                }, 0);
			}
		},
		data: function () {
			return {
				categories: [],
				income: []
			}
		}
	}
</script>



