import Vue from 'vue';
import * as firebase from 'firebase';
import moment from 'moment';

import utilsService from '@/services/utils-service';
import authService from '@/services/auth-service';
import dateService from '@/services/date-service';


export default {
	events: new Vue(),

	data: {
		categories: null,
		income: null,
	},

	init: function () {
		dateService.events.$on('selectedChanged', date => {
			this.dbFetch(date.year, date.month);
			console.log('income react')
		});
	},

	getCategories: function () {
		return this.data.categories;
	},

	addNewItem: function () {
		const id = utilsService.generateId();
		Vue.set(this.data.income, id, {
			amount: null,
			description: '',
			category: 'unknown',
			date: +new Date(),
			id: id
		});
		console.log(this.data.income);

	},

	delete: function (id) {
		this.dbDeleteItem(id);
		this.data.income[id] = null;
		delete this.data.income[id];
	},

	dbFetch: function (year, month) {
		const uid = authService.getUid();
		if (!uid) {
			console.warn('There is no uid at data fetch');
			return;
		}

		const db = firebase.database();
		const timestamp = dateService.getMonthTimestamps(+year, +month);
		db.ref('users/' + uid + '/data/income').orderByChild('date').startAt(timestamp.start).endAt(timestamp.end).once('value').then(snapshot => {
			this.data.income = snapshot.val() || {};
			this.events.$emit('updated', this.data);
		});

		db.ref('users/' + uid + '/data/categories/income').once('value').then(snapshot => {
			this.data.categories = snapshot.val() || {};
			//console.log(snapshot.val());
			this.events.$emit('updated', this.data);
		});
	},
	dbPushItem: function (id) {
		const uid = authService.getUid();
		if (!uid) {
			console.warn('There is no uid, push income');
			return;
		}
		const item = this.data.income[id];
		const db = firebase.database();
		db.ref('users/' + uid + '/data/income/'+id).set(item);

		if (this.data.categories && this.data.categories.indexOf(item.category) == -1) {
			this.data.categories.push(item.category);
			db.ref('users/' + uid + '/data/categories/income').set(this.data.categories);
		}
	},

	dbDeleteItem: function (id) {
		const uid = authService.getUid();
		if (!uid) {
			console.warn('There is no uid, push income');
			return;
		}

		const db = firebase.database();
		db.ref('users/' + uid + '/data/income/'+id)
			.remove()
				.then(function() {
					console.log("Remove succeeded.")
				}).catch(function(error) {
					console.log("Remove failed: " + error.message)
				});
	}
}


