<style scoped>
    .outcome-widget {

    }

    .debug-table {
        margin-top: 20px;
        position: absolute;
    }

    a {
        color: #FFF;
    }

    .addone {
        background: #FFF;
        border: 0;
        height: 32px;
        width: 100%;
        color: #00BCD4;
        font-size: 32px;
        outline: none;
        border-top: 1px solid #00bcd4;
        cursor: pointer;
    }
</style>

<template>
    <div class="widget outcome-widget">
        <h1>Outcome</h1>
        <div class="widget-body">
            <table class="outcome-table table table-lined">
                <app-outcome-item
                        v-for="(item, index) in outcome"
                        :key="index"
                        :item="item"
                ></app-outcome-item>
            </table>

            <button @click="newItem" class="addone">+</button>
        </div>
    </div>

</template>

<script>
	import authService from '@/services/auth-service';
	import dateService from '@/services/date-service';
	import outcomeService from '@/services/outcome-service';


	export default {
		name: 'app-outcome-widget',

		created: function () {
			outcomeService.events.$on('updated', (data) => {
				this.$data.outcome = outcomeService.getOutcomeArray(this.$data.selectedDate)
				this.$data.categories = data.categories;
			});

			dateService.events.$on('selectedChanged', (selected) => {
				this.$data.selectedDate = selected
				this.$data.outcome = outcomeService.getOutcomeArray(selected)
            })

			this.$data.outcome = outcomeService.getOutcomeArray(this.$data.selectedDate)
			this.$data.categories = outcomeService.getCategories()
		},

		methods: {
			newItem: function () {
				outcomeService.addNewItem();
				setTimeout(()=>{
					const inputs = this.$el.querySelectorAll('input.amount');
					inputs[inputs.length-1].focus();
				}, 0);
			}
		},
		data: function () {
			return {
				categories: [],
				outcome: [],
				selectedDate: dateService.getMonthTimestamps()
			}
		}
	}
</script>




