// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import router from './router'


import App from './App'
import Header from '@/components/Header'
import Calendar from '@/components/Calendar';
import Income from '@/components/Income';
import IncomeItem from '@/components/IncomeItem';
import Outcome from '@/components/Outcome';
import OutcomeItem from '@/components/OutcomeItem';
import SummaryChart from '@/components/Statistic/SummaryChart';

import * as firebase from 'firebase';

import authService from '@/services/auth-service';
import outcomeService from '@/services/outcome-service';
import incomeService from '@/services/income-service';


Vue.component('app-header', Header);
Vue.component('app-calendar-widget', Calendar);

Vue.component('app-outcome-widget', Outcome);
Vue.component('app-outcome-item', OutcomeItem);

Vue.component('app-income-widget', Income);
Vue.component('app-income-item', IncomeItem);

Vue.component('app-chart-summary', SummaryChart);



Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
	template: '<App/>',
	components: {
		App,
		Calendar,
		Outcome,
		Income,
		OutcomeItem,
		SummaryChart
	}
});


// Initialize Firebase
const config = {
	apiKey: "AIzaSyAiosYDEN9e8PwUyNotnUD5YMqBHQmkiKY",
	authDomain: "budget-86974.firebaseapp.com",
	databaseURL: "https://budget-86974.firebaseio.com",
	projectId: "budget-86974",
	storageBucket: "budget-86974.appspot.com",
	messagingSenderId: "98263644403"
};

firebase.initializeApp(config);
authService.init();
outcomeService.init();
incomeService.init();



