import Vue from 'vue'
import * as firebase from 'firebase'
import moment from 'moment'

import utilsService from '@/services/utils-service'
import authService from '@/services/auth-service'
import dateService from '@/services/date-service'


export default {
	events: new Vue(),
	
	data: {
		categories: null,
		outcome: null
	},
	
	init: function () {
		if (authService.authorized()) {
			this.dbFetch()
		} else {
			authService.events.$on('authStateChanged', () => this.dbFetch())
		}
	},
	
	getCategories: function () {
		if (this.data.categories && this.data.categories.length) {
			return this.data.categories.slice()
		}
		return []
	},
	
	getOutcomeArray: function ({year, month} = {}) {
		const timestamps = year && month ? dateService.getMonthTimestamps(year, month) : {
			start: 0,
			end: Infinity
		}
		
		const filteredArray = []
		
		for (let id in this.data.outcome) {
			const item = this.data.outcome[id]
			if (item.date > timestamps.start && item.date < timestamps.end) {
				filteredArray.push({
					...item,
					amount: +item.amount
				})
			}
		}
		
		filteredArray.sort((a, b) => a.date - b.date)
		return filteredArray
	},
	
	addNewItem: function () {
		const id = utilsService.generateId()
		Vue.set(this.data.outcome, id, {
			amount: null,
			description: '',
			category: 'unknown',
			date: +new Date(),
			id: id
		})
		
		this.events.$emit('updated', {
			outcome: this.getOutcomeArray(),
			categories: this.data.categories
		})
	},
	
	delete: function (id) {
		this.dbDeleteItem(id)
		this.data.outcome[id] = null
		delete this.data.outcome[id]
	},
	
	dbFetch: function () {
		const uid = authService.getUid()
		if (!uid) {
			console.warn('There is no uid at data fetch')
			return
		}
		
		const db = firebase.database()
		db.ref('users/' + uid + '/data/outcome').orderByChild('date').once('value').then(snapshot => {
			this.data.outcome = snapshot.val() || {}
			this.events.$emit('updated', {
				outcome: this.getOutcomeArray(),
				categories: this.data.categories
			})
		})
		
		db.ref('users/' + uid + '/data/categories/outcome').once('value').then(snapshot => {
			this.data.categories = snapshot.val() || {}
			this.events.$emit('updated', {
				outcome: this.getOutcomeArray(),
				categories: this.data.categories
			})
		})
	},
	
	
	dbPushItem: function (id) {
		const uid = authService.getUid()
		if (!uid) {
			console.warn('There is no uid, push outcome')
			return
		}
		
		const item = this.data.outcome[id]
		
		const db = firebase.database()
		db.ref('users/' + uid + '/data/outcome/' + id).set(item)
		
		if (this.data.categories && this.data.categories.indexOf(item.category) == -1) {
			this.data.categories.push(item.category)
			db.ref('users/' + uid + '/data/categories/outcome').set(this.data.categories)
		}
	},
	
	dbDeleteItem: function (id) {
		const uid = authService.getUid()
		if (!uid) {
			console.warn('There is no uid, push outcome')
			return
		}
		
		const db = firebase.database()
		db.ref('users/' + uid + '/data/outcome/' + id)
			.remove()
			.then(function () {
				console.log('Remove succeeded.')
			}).catch(function (error) {
			console.log('Remove failed: ' + error.message)
		})
	},
	
	generateId: () => Math.abs(~~(Math.random() * 1e15))
}


